@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light+Two&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@100;300;400;500;600;700;800;900&display=swap);

.App{background-color:#323031;min-height:100vh;color:#fff}.nav__list{display:flex;justify-content:center;background-color:#48c78e;font-size:16px}.nav__item{padding:10px 20px;text-transform:uppercase;color:#2c3b5a;transition:all ease-in-out 0.4s}.nav__item:hover,.nav__item:active{background-color:rgba(242,241,239,0.4)}.form{width:500px}.container{max-width:1200px;margin:0 auto}.www{background:linear-gradient(90deg, #e63946, #14213d, #23a6d5, #606c38);background-size:200% 200%;animation:gradient 10s ease infinite}@keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}.section{outline:2px dashed lightpink}.section__title{font-size:18px;text-align:center;padding:20px 0;color:#9a8c98}

*{box-sizing:border-box}body{margin:0}ul,ol{margin:0;padding:0;list-style:none}button{border:0}

.section{max-width:1200px;margin:0 auto}.char__list{display:flex;flex-flow:row wrap;justify-content:center;margin:0 auto}P{overflow:hidden;text-overflow:ellipsis;font-size:10px}.card__name{font-family:"IBM Plex Sans", sans-serif;font-weight:100;text-transform:uppercase;font-size:16px}figure{margin:0}figure img{width:100%;opacity:0.8}figure img:hover{opacity:1;transition:all 0.3s linear;box-shadow:2px 2px 20px #48c78e}.column{box-sizing:border-box;flex:1 1 100%;justify-content:space-between;margin:10px}.card-style{border-image-slice:1;box-shadow:0 1px 2px 0 rgba(0,0,0,0.4);transition:all 0.25s linear}.card-style:hover{box-shadow:2px 2px 20px #48c78e}@media (min-width: 500px){.column{flex-grow:0;flex-shrink:0;flex-basis:calc(100%/2 - 40px)}}@media (min-width: 900px){.column{flex-grow:0;flex-shrink:0;flex-basis:calc(100%/3 - 40px)}}.select__box{text-align:center}

hr{margin:10px 0}.modalw{height:100vh;width:100vw;background-color:rgba(0,0,0,0.7);position:fixed;z-index:100;top:0;left:0;display:flex;align-items:center;justify-content:center;color:#000}.modal__content{padding:20px;border-radius:4px;background-color:rgba(255,255,255,0.6);width:320px}.intro{display:flex;flex-direction:column;cursor:pointer}.intro__title{display:flex;justify-content:space-between;margin:4px}.intro__content{font-size:16px;padding:10px 0}.intro__timezone{text-align:center;font-size:22px}

.char-select{background-color:#9a8c98;width:140px;height:40px;padding:4px;outline:none;transition:all ease-in-out 0.2s}.char-select:active,.char-select:hover{box-shadow:2px 2px 20px #48c78e;background-color:#ffe8d6}

.section{max-width:1200px;margin:0 auto;padding:0}.episodes__list--items{display:flex;flex-direction:column;justify-content:center;align-content:center}.episodes__input{width:300px;margin:0 auto}.episodes__input{width:140px;height:40px;padding:10px;outline:none;border:none;cursor:context-menu;transition:all ease-in-out 0.4s;background-color:#9a8c98;margin-bottom:20px}.episodes__input:active,.episodes__input:hover{box-shadow:2px 2px 20px #48c78e;background-color:#ffe8d6}.episodes__table{cursor:pointer;margin:0 auto;background-color:#9a8c98}tr{transition:all ease-in-out 0.4s}.table.is-hoverable.is-striped tbody tr:not(.is-selected):hover{background-color:#ffe8d6;box-shadow:2px 2px 20px #48c78e}

.locations__table{cursor:pointer;margin:0 auto;background-color:#9a8c98;width:1000px}.locations__select,.locations__input{background-color:#9a8c98;width:140px;height:40px;padding:8px;outline:none;border:none;cursor:context-menu;transition:all ease-in-out 0.4s}.locations__select:active,.locations__select:hover{box-shadow:2px 2px 20px #48c78e;background-color:#a9a4a4}.locations__input:active,.locations__input:hover{box-shadow:2px 2px 20px #48c78e;background-color:#ffe8d6}.locations__form{display:flex;align-items:center;justify-content:center;padding:0 0 20px 0}

.container{width:500px;margin:40px auto;padding:20px}form{display:inline-block;width:100%}.title{color:white;font-size:14px;margin:0 !important}.todo-item,.todo-input{font-size:16px;width:100%;height:40px;padding:5px 10px;display:flex;align-items:center;align-content:center;transition:all ease-in-out 0.4s;background-color:#9a8c98;margin-bottom:10px}.todo-input:active,.todo-input:hover{box-shadow:2px 2px 20px #48c78e;background-color:#ffe8d6}.todo-item{position:relative;padding:30px 10px}.todo-item input[type='checkbox']{margin-right:10px}.delete{position:absolute;right:10px;top:20px;display:none}.todo-item:hover .delete{display:inline-block}.delete:hover{cursor:pointer}

